<template>
    <div id="app">
        <router-view/>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                transitionName: ''
            }
        },
        watch: {
            '$route' (to) {
                if (to.name === 'login') {
                    this.transitionName = 'slide-right';
                }
            }
        }
    }
</script>

